.footer_container{
    max-width: 1440px;
    margin: 0px auto;
    padding-left: 200px;
    padding-right: 200px;
    padding-top: 24px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.footer-1{
    margin-left: -207px;
}
.footer_container h3{
    color: #6d6d6d;
    font-size: 16px;
    font-weight: 400;
}

li{
    padding: 24px 0px;
    list-style: none;
    font-size: 16px;
    color: #101010;
    font-weight: 700;
}
@media (max-width: 835px){
    .footer_container{
        margin: 0px auto;
        padding-left: 51px;
        padding-right: 51px;
        padding-top: 24px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .footer-1{
        margin-left: -100px;
    }
}
@media (max-width: 430px){
    .footer_container{
        margin: 0px auto;
        padding-left: 25px;
        padding-right: 25px;
        padding-top: 24px;
        display: flex;
        flex-direction: column;
    }
    .footer-1{
        margin-left: 0px;
        margin-top: 20px;
    }
    li{
        padding: 15px 0px;
        list-style: none;
        font-size: 16px;
        color: #101010;
        font-weight: 700;
    }
    .footer_menu{
        margin-bottom: 30px;
    }
}