
.page_content{
    margin-top: -5px;
    background-color: #f5f5f5;
}
.page_container{
    max-width: 1440px;
    margin: 0px auto;
    padding-left: 200px;
    padding-right: 200px;
}
.about{
    padding-top: 56px;
    padding-bottom: 62px;
    font-size: 20px;
    font-weight: 400;
    line-height: 27px;
    color: #6d6d6d;
}
.first_flex{
    display: flex;
    flex-wrap: wrap;
    gap: 59px;
    align-items: center;
    margin-bottom: 62px;
}
.item_1{
    width: 490px;
}
.item_2 img{
    height: 302px;
    width: 490px;
}
.item_1 h1{
    font-size: 32px;
    font-weight: 600;
    color: #101010;
    padding-bottom: 14px;
}
.item_1 p{
    font-size: 20px;
    line-height: 27px;
    font-weight: 400;
    color: #6d6d6d;
}
.second_flex{
    display: flex;
    flex-wrap: wrap;
    gap: 53.72px;
    align-items: center;
    margin-bottom: 62px;
}
.second-item h1{
    font-size: 32px;
    font-weight: 600;
    color: #101010;
    padding-bottom: 14px;
}
.second-item{
    width: 490px;
}
.second-item p{
    font-size: 20px;
    line-height: 27px;
    font-weight: 400;
    color: #6d6d6d;
}
.first-item img{
    height: 308px;
    width: 490px;
   
}
.history{
   margin-bottom: 43px;
}
.history h1{
    padding-bottom: 14px;
    color: #101010;
    font-size: 32px;
    font-weight: 600;
}
.history p{
    color: #6d6d6d;
    font-size: 20px;
    line-height: 27px;
    font-weight: 400;
}
.team{
    margin-bottom: 38px;
}
.team h1{
    padding-bottom: 14px;
    color: #101010;
    font-size: 32px;
    font-weight: 600;
}
.team p{
    color: #6d6d6d;
    font-size: 20px;
    line-height: 27px;
    font-weight: 400;
}
