.resources{
    margin-top: 62px;
    display: flex;
    flex-wrap: wrap;
}
.resources_card{ 
    margin-top: 14px;
   display: flex;
   flex-wrap: wrap;
    gap: 30px;
    margin-bottom: 213px;
}
.resources_container h2{
    font-size: 32px;
    font-weight: 600;
    color: #000;
}
.first_card{
    height: 302px;
    width: 490px;
    background-color: #fff;
    padding-top: 70px;
    padding-bottom: 88px;
    padding-left: 43px;
    padding-right: 97px;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.15);  
}
.first_card h3{
    font-size: 24px;
    font-weight: 600;
    color: #000;
    padding-bottom: 14px;
}
.first_card p{
    color: #6d6d6d;
    font-size: 20px;
    font-weight: 400;
    line-height: 27px;
    padding-bottom: 16px;
}
.second_card{
    height: 302px;
    width: 490px;
    background-color: #fff;
    padding-top: 70px;
    padding-bottom: 88px;
    padding-left: 43px;
    padding-right: 97px;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.15);
}
.second_card h3{
    font-size: 24px;
    font-weight: 600;
    color: #000;
    padding-bottom: 14px;
}
.second_card p{
    color: #6d6d6d;
    font-size: 20px;
    font-weight: 400;
    line-height: 27px;
    padding-bottom: 16px;
}

a{
    font-size: 20px;
    font-weight: 400;
    color: #03296f
}
@media (max-width: 835px){
    .resources_card{ 
        margin-bottom: 150px;
    }
    .first_card{
        width: 338px;
        padding: 30px;
    }
    .first_card p{
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
    }
    .second_card{
        height: 302px;
        width: 338px;
        padding: 30px;
    }
    .second_card p{
        font-size: 18px;
        line-height: 24px;
    }
    .resources{
        margin-top: 48px;
        display: flex;
        flex-wrap: wrap;
    }
   
}
@media (max-width: 430px){
    .resources_card{ 
        margin-top: 8px;
       display: flex;
       flex-direction: column;
        gap: 33px;
        margin-bottom: 127px;
    }
    .resources{
        margin-top: 32px;
    }
    .resources_container h2{
        font-size: 20px;
    }
    .first_card{
        height: 302px;
        width: 317px;
        padding-top: 54px;
        padding-bottom: 79px;
        padding-left: 23px;
        padding-right: 46px;
    }
    .first_card h3{
        font-size: 20px;
    }
    .first_card p{
        font-size: 16px;
        line-height: 21px;
    }
    .second_card{
        height: 302px;
        width: 317px;
        padding-top: 54px;
        padding-bottom: 79px;
        padding-left: 23px;
        padding-right: 46px;
    }
    .second_card h3{
        font-size: 20px;
    }
    .second_card p{
        font-size: 16px;
        line-height: 21px;
    }
    a{
        font-size: 16px;
    }
    
}