.navbar{
    background-color: #fff;
    height: 88px;
    padding: 16px 96px; 
}
.navbar_container{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.menu{
    display: flex;
    gap: 40px;
    font-size: 16px;
    font-weight: 700;
    list-style: none;
}
.hamburger{
    display: none;
    cursor: pointer;
}

li{
    cursor: pointer;
    color: #101010;
}
.button{
    display: flex;
    gap: 20px;
}
.btns1{
   padding: 10px 15px;
   color: #0652dd;
   border-radius: 5px;
   background-color: #fff;
   cursor: pointer;
   font-family:  'Manrope', sans-serif;
   border: 1px solid #0652dd;
}
.btns2{
    padding: 10px 15px;
    font-family:  'Manrope', sans-serif;
    background-color: #0652dd;
    border-radius: 5px;
    color: #fff;
    cursor: pointer;
    border: 1px solid #0652dd;
 }
 @media (max-width: 835px){
    .menu{
        display: none;
    }
    .hamburger{
        display: block;
    }
    .navbar{
        padding: 16px 48px;
    }
 }
 @media (max-width:430px){
    .navbar{
        background-color: #fff;
        height: 48px;
        padding: 8px 24px; 
    }
    .btns1{
        display: none;
    }
    .btns2{
        display: none;
    }
    .navbar_container img{
        height: 27px;
    }
 }