.images{
    height: 344px;
    width: 317px;
}
.name{
    margin-top: 23px;
    margin-bottom: 8px;
    font-size: 32px;
    font-weight: 600;
    color: #101010;
}
.stack{
    font-size: 24px;
    font-weight: 400;
    color: #101010;
    margin-bottom: 8px;
}
.details{
    font-size: 20px;
    font-weight: 400;
    color: #6d6d6d;
    line-height: 27px;
}
.team_container{
    width: 317px;
}
.team_box{
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
}
@media (max-width: 835px){
    .images{
        height: 330px;
        width: 345px;
    }
    .name{
        font-size: 24px;
        margin-top: 17px;
        margin-bottom: 8px;
    }
    .stack{
        font-size: 20px;
    }
    .details{
        font-size: 18px;
        line-height: 24px;
    }
    .team_box{
        display: flex;
        flex-wrap: wrap;
        gap: 42px;
    }
    .team_container{
        width: 317px;
    }
}
@media (max-width: 430px){
    .images{
        height: 302px;
        width: 317px;
    }
    .name{
    font-size: 20px;
    }
    .stack{
        font-size: 16px;
    }
    .details{
        font-size: 16px;
        line-height: 21px;
    }
    .team_box{
        display: flex;
        flex-wrap: wrap;
        gap: 31px;
    }
    
}