*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.banner_text{
    position: relative;
    width: 100%;
    margin: 0px auto;
}
.banner_text img{
    width: 100%;
    height: 216px;
}
.banner_text h1{
    position: absolute;
    width: 100%;
    text-align: center;
    top: 70px;
    font-size: 56px;
    font-weight: 700;
    color: #fff;
}
@media (max-width: 430px){
    .banner_text h1{
        position: absolute;
        width: 100%;
        text-align: center;
        top: 70px;
        font-size: 32px;
        font-weight: 700;
        color: #fff;
    }
}