.license{
    height: 52px;
    width: auto;
    background-color: #f2f2f7;
}
.license_container{
    max-width: 1440px;
    margin: 0px auto;
    padding: 18px 200px;
    display: flex;
    gap: 20px;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}
.rights,
.terms,
.privacy{
    font-size: 12px;
    font-weight: 400;
    color: #6d6d6d;
}
@media (max-width: 835px){
   .license_container{
        margin: 0px auto;
        padding: 18px 51px;
    }
}
@media (max-width: 430px){
    .license_container{
        margin: 0px auto;
        padding: 18px 25px;
    }
    .license{
        height: 80px;
    }
}